.img-container {
  position: relative;
  display: block;

  .user-img {
    display: block;
    border: none;
    background-color: transparent;
    border-radius: 50%;

    cursor: pointer;

    &.xsmall {
      width: rem(24px);
      height: rem(24px);
    }

    &.small {
      width: rem(32px);
      height: rem(32px);
    }

    &.medium {
      width: rem(34px);
      height: rem(34px);
    }

    &.large {
      width: rem(40px);
      height: rem(40px);
    }

    &.xlarge {
      width: rem(50px);
      height: rem(50px);
    }

    &.xxlarge {
      width: rem(86px);
      height: rem(86px);
    }

    &.no-padding {
      padding: 0;
    }

    &.padding {
      padding: rem(4px);
    }

    &:hover.circle {
      background-color: $icon-hover;
      border-radius: 50%;
    }

    &::after {
      content: '';
      background-color: $icon-hover;
      position: absolute;
      display: none;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      border-radius: 50%;
      opacity: 0.4;
    }
  }

  &:hover::after {
    display: block;
  }
}